<h2 id="drawer-抽屉" tabindex="-1"><a class="header-anchor" href="#drawer-抽屉" aria-hidden="true">#</a> Drawer 抽屉</h2>
<h3 id="用法" tabindex="-1"><a class="header-anchor" href="#用法" aria-hidden="true">#</a> 用法</h3>
<h4 id="基本用法" tabindex="-1"><a class="header-anchor" href="#基本用法" aria-hidden="true">#</a> 基本用法</h4>
<div class="component-box">
  <div class="component-box-top"><ClientOnly>
    <wd-button @click="centerDialogVisible = true">show drawer</wd-button>
    <wd-drawer
      v-model="centerDialogVisible"
      title="Notice"
      width="40%"
      :show-close="true"
      @open="openHandler"
      @before-open="beforeOpen"
      @before-close="beforeClose"
      :append-to-body="true"
      :closeOnClickModal="false"
      :destroy-on-close="false"
      placement="right"
      :appear="true"
    >
      <span>
        drawer
      </span>
      <template #footer>
        <wd-button type="primary" @click="centerDialogVisible = false"
          >确定</wd-button
        >
      </template>
    </wd-drawer>
    </ClientOnly>
  </div>
  <div class="component-box-bottom">
    <div class="component-title">抽屉组件</div>
    <div class="component-desc">
      有些时候, Dialog 组件并不满足我们的需求, 比如你的表单很长, 亦或是你需要临时展示一些文档, Drawer 拥有和 Dialog 几乎相同的 API, 在 UI 上带来不一样的体验
    </div>
  </div>
</div>
