<table>
<thead>
<tr>
<th style="text-align:center">参数</th>
<th style="text-align:center">说明</th>
<th style="text-align:center">类型</th>
<th style="text-align:center">可选值</th>
<th style="text-align:center">默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:center">appendToBody</td>
<td style="text-align:center">遮罩层是否插入至 body 元素上，若为 false，则遮罩层会插入至 Drawer 的父元素上</td>
<td style="text-align:center">boolean</td>
<td style="text-align:center">true / false</td>
<td style="text-align:center">false</td>
</tr>
<tr>
<td style="text-align:center">customClass</td>
<td style="text-align:center">自定义类名</td>
<td style="text-align:center">string</td>
<td style="text-align:center">''</td>
<td style="text-align:center">''</td>
</tr>
<tr>
<td style="text-align:center">closeOnClickModal</td>
<td style="text-align:center">点击遮罩是否关闭Drawer</td>
<td style="text-align:center">boolean</td>
<td style="text-align:center">true / false</td>
<td style="text-align:center">true</td>
</tr>
<tr>
<td style="text-align:center">destroyOnClose</td>
<td style="text-align:center">关闭Drawer时是否移除元素</td>
<td style="text-align:center">boolean</td>
<td style="text-align:center">true / false</td>
<td style="text-align:center">false</td>
</tr>
<tr>
<td style="text-align:center">showClose</td>
<td style="text-align:center">是否显示关闭按钮</td>
<td style="text-align:center">boolean</td>
<td style="text-align:center">true / false</td>
<td style="text-align:center">true</td>
</tr>
<tr>
<td style="text-align:center">title</td>
<td style="text-align:center">Drawer标题</td>
<td style="text-align:center">string</td>
<td style="text-align:center">''</td>
<td style="text-align:center">''</td>
</tr>
<tr>
<td style="text-align:center">openDelay</td>
<td style="text-align:center">延迟打开Drawer</td>
<td style="text-align:center">number</td>
<td style="text-align:center">-</td>
<td style="text-align:center">0</td>
</tr>
<tr>
<td style="text-align:center">closeDelay</td>
<td style="text-align:center">延迟关闭Drawer</td>
<td style="text-align:center">number</td>
<td style="text-align:center">-</td>
<td style="text-align:center">0</td>
</tr>
<tr>
<td style="text-align:center">top</td>
<td style="text-align:center">顶部margin</td>
<td style="text-align:center">string</td>
<td style="text-align:center">-</td>
<td style="text-align:center">15vh</td>
</tr>
<tr>
<td style="text-align:center">width</td>
<td style="text-align:center">Drawer宽度</td>
<td style="text-align:center">string</td>
<td style="text-align:center">-</td>
<td style="text-align:center">50%</td>
</tr>
</tbody>
</table>
<h3 id="事件" tabindex="-1"><a class="header-anchor" href="#事件" aria-hidden="true">#</a> 事件</h3>
<table>
<thead>
<tr>
<th style="text-align:center">事件名称</th>
