</tr>
</thead>
<tbody>
<tr>
<td style="text-align:center">close</td>
<td style="text-align:center">Drawer关闭时触发</td>
<td style="text-align:center">Function()</td>
</tr>
<tr>
<td style="text-align:center">before-close</td>
<td style="text-align:center">Drawer关闭之前触发</td>
<td style="text-align:center">Function()</td>
</tr>
<tr>
<td style="text-align:center">open</td>
<td style="text-align:center">Drawer弹出时触发</td>
<td style="text-align:center">Function()</td>
</tr>
<tr>
<td style="text-align:center">before-open</td>
<td style="text-align:center">Drawer弹出之前触发</td>
<td style="text-align:center">Function()</td>
</tr>
</tbody>
</table>
